.span{
    font-size: 30px;
    font-family: 'Indie Flower', cursive;
    color: #1d60e0;
}
.cardContainer{
    margin-top: 180px;
    max-width: 400px;
    min-height: 300px;
    height: 100%;
    position: relative;
}

.card{

    width:426px;
    min-height: 300px;
    background-color:#e4dfdc ;
    border-radius: 30px;
     border: 4px;
    border-white:40px;
    padding-top: 24px;
    cursor: pointer;
}
@media   (max-width: 707px){
    .card{
        width:360px;
    }
}
@media   (max-width: 400px){
    .card{
        width:330px;
    }
}

.cardLogo{
    margin-bottom: 6px;
}
.cardTitle{
    font-size: 40px;
    margin-bottom: 6px;
 font-weight: bold;
    font-family: 'Indie Flower', cursive;
    color: #1d60e0;
}
.cardSubtitle{
    font-family: 'Indie Flower', cursive;
    max-width: 300px;
    color: #1d60e0;
    margin-bottom: 6px;
}
.priceWrapper{
    display: flex;
    align-items: center;
    gap:20px;
    margin-bottom: 12px;

}

.price{
    font-size: 24px;
    font-weight: bold;
    color: #c609f1;

}
.colors{
    display: flex;
    gap: 10px;

}
.color{
    width: 28px;
    height: 28px;
border-radius: 15%;
margin-bottom: 5px;
}
.cardImage{
    position: absolute;
    top: 95px;
    right:-70px;
    /*left:272px;*/

    /*width: 320px;*/

}
@media   (max-width: 707px){
    .cardImage{
        position: absolute;
        top: 173px;
        right:-64px;

    }
    .img{

        width: 80%;
    }
}
@media   (max-width: 400px){
    .cardImage{
        position: absolute;
        top: 183px;
        right:-74px;

    }
    .img{

        width: 60%;
    }
}

